import { Col, Form, Row } from "antd";
import React, { useState, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import Paper from "../../../../../components/Paper/Paper";
import SaveCancelButtons from "../../../../Shipment/components/SaveCancelButtons";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../utilz/i18n";
import AddTenderTab from "./AddTenderTab";
import FiledCard from "../../../../../components/Cards/FiledCard";
import { validateForms } from "../../../../../../utilz/utilz";
import { useSelector } from "react-redux";

export default function AddTender() {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const [activeKey, setActiveKey] = useState(["1", "2", "3", "4", "5"]);
  const navigate = useNavigate();
  const [form] = Form.useForm();
  const [tenderInformationForm] = Form.useForm();
  const [documentForm] = Form.useForm();
  const [bitParameterForm] = Form.useForm();
  const { selectedLaneListItems } = useSelector(
    (state) => state.procurementLane
  );

  const handleSubmit = async (submittedForm, { forms }) => {
    if (submittedForm === "saveButtonForm") {
      debugger;
      try {
        const formList = [
          validateForms(forms.shipmentDetailsForm, "shipmentDetailsForm"),
          //validateForms(forms?.documentForm, "documentForm"),
          // validateForms(forms?.bitParameterForm, "bitParameterForm"),
        ];
        await Promise.all(formList);
      } catch (e) {
        console.log(e);
        return;
      }
      const tenderInfoFormValue = { ...tenderInformationForm.getFieldsValue() };
      const laneInfo = selectedLaneListItems?.filter(item=> item?.isSelected === true).map(item=>{
        return {
          LaneID:item.laneId
        }
      })
      
      let requestFormat = {
        ProjectID: tenderInfoFormValue?.projectname || '',
        Currency: tenderInfoFormValue?.currency || '',
        ServiceCategory: tenderInfoFormValue?.serviceCategory ||'',
        TenderName: tenderInfoFormValue?.tenderName || '',
        TenderDescription: tenderInfoFormValue?.TenderStatus || '',
        TransportationMode: tenderInfoFormValue?.TenderStatus || '',
        TenderID: 0,
        CargoType: tenderInfoFormValue?.cargoType || '',
        ExchangeDate: `${
          tenderInfoFormValue.exchangeDate.format("yyyy-MM-DD") + "T"
        }${tenderInfoFormValue.exchangeDate.format("h:mm:ss")}`,
        EquipmentType: tenderInfoFormValue?.equipmentType || '',
        TenderstartDate:`${
          tenderInfoFormValue.tenderstartDate.format("yyyy-MM-DD") + "T"
        }${tenderInfoFormValue.tenderstartTime.format("h:mm:ss")}`,
        TenderendDate: `${
          tenderInfoFormValue.tenderendDate.format("yyyy-MM-DD") + "T"
        }${tenderInfoFormValue.tenderendTime.format("h:mm:ss")}`,
        ModeofTender: tenderInfoFormValue?.modeofTender || '',
        TenderNote: null,
        TenderReferenceOne: tenderInfoFormValue?.tenderReferenceOne || '',
        TenderReferenceTwo: tenderInfoFormValue?.tenderReferenceTwo || '',
        LaneDetails: laneInfo,
        BidParameters: [
          {
            Sequence: 1,
            IsRequired: false,
            FieldName: "Tax",
            FieldDataTypeCode: "Num",
            FieldValue: "5",
            DisplayName: "Tax",
            ParameterType: "Numeric Input",
            ParameterTypeCode: "NTXT",
            IsHistorical: false,
            IsRepeated: false,
          },
          {
            Sequence: 2,
            IsRequired: false,
            FieldName: "Price",
            FieldDataTypeCode: "Num",
            FieldValue: "20",
            DisplayName: "Price",
            ParameterType: "Numeric Input",
            ParameterTypeCode: "NTXT",
            IsHistorical: false,
            IsRepeated: false,
          },
        ],
        TransportProviders: [{ CarrierKey: 1627, CarrierID: 0 }],
        TenderDocument: [
          {
            DocumentName: "NotificationList (3).xlsx",
            DocumentPath: "sssssss",
            DocType: "PL",
            Comments: "Test Comment",
            FileExtension: ".xlsx",
          },
        ],
      };
    }
  };
  return (
    <Form.Provider
      className={"overflow-hidden h-100"}
      onFormFinish={handleSubmit}
    >
      <Row
        className={"flex-direction-column flex-nowrap overflow-hidden h-100"}
      >
        <Col className="mb-15">
          <Paper className={"p-15 border-radius-bottom-none"}>
            <Row align="middle" justify="space-between">
              <Col>
                <Form form={form}>
                  <FiledCard title={"Create Tender"}></FiledCard>
                </Form>
              </Col>
              <Col>
                <Form name="saveButtonForm">
                  <SaveCancelButtons
                    onSave={() => {}}
                    onCancel={() => navigate(-1)}
                  />
                </Form>
              </Col>
            </Row>
          </Paper>
        </Col>
        <Col className="flex-1 custom-scroll-bar">
          <AddTenderTab
            tenderInformationForm={tenderInformationForm}
            documentForm={documentForm}
            bitParameterForm={bitParameterForm}
            activeKey={activeKey}
            setActiveKey={setActiveKey}
          />
        </Col>
      </Row>
    </Form.Provider>
  );
}
