import React, { Fragment, useEffect, useMemo, useState } from "react";
import SaveCancelButton from "../../../../../components/Common/SaveCancelButton";
import SecondaryLabel from "../../../../../components/Labels/SecondaryLabel";
import { Card, Col, Row } from "antd";
import FiledCard from "../../../../Shipment/components/FiledCard";
import HorizontalBar from "../../../../../components/Bars/HorizontalBar";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";
import { ReactComponent as LocationIcon } from "../../../../../../assets/icons/location-pin-green.svg";
import SvgConverter from "../../../../../components/Common/SvgConverter";
import { selectedLaneList } from "../../../../../../redux/Procurement/RouteConfig/Lane/ProcurementLaneActions";
import { useDispatch } from "react-redux";

function TenderLaneInformationInfo() {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const { selectedLaneListItems } = useSelector(
    (state) => state.procurementLane
  );
  const dispatch = useDispatch();

  return (
    <div className="overflow-y-inherit d-flex flex-direction-column">
      <div className="d-flex flex-direction-column overflow-hidden br-4">
        <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
          {selectedLaneListItems.map((item, index) => (
            <Card
              key={index}
              className="mb-15"
              title={
                <Row>
                  <Col sm={24} className="d-flex gap-half align-items-center">
                    <Fragment>
                      <FiledCard
                        title={`${t(
                          "translation:procurement.routeConfigurationTabs.lane"
                        )}:`}
                        textClass="d-flex align-items-center gap-half"
                      >
                        <div className="d-flex align-items-center gap-1">
                          <SecondaryLabel className={"f-14 text-success"}>
                            {item?.originName}
                          </SecondaryLabel>
                          <div style={{ width: "3em" }}>
                            <HorizontalBar />
                          </div>
                          <SecondaryLabel className="f-14 text-danger-3">
                            {item?.destinationName}
                          </SecondaryLabel>
                        </div>
                      </FiledCard>
                    </Fragment>
                  </Col>
                </Row>
              }
              extra={
                <SaveCancelButton
                  hideEditIcon={true}
                  editMode={false}
                  handleRemove={() => {
                    let temp = [...selectedLaneListItems];
                    let remainingItems = temp.filter((i, idx)=>idx != index);
                    dispatch(selectedLaneList(remainingItems))
                  }}
                />
              }
            >
              <Row>
                <Fragment>
                  <Col sm={24}>
                    <div className="d-flex">
                      <div className="w-49 pr-15">
                        <Row>
                          <Col
                            sm={24}
                            className="d-flex w-100 gap-1 align-items-center"
                          >
                            <SvgConverter
                              Icon={LocationIcon}
                              color={"success"}
                            />
                            <SecondaryLabel className={"f-14 text-success"}>
                              {`${t("translation:default.fields.origin")}:`}
                            </SecondaryLabel>
                            <hr className="w-75 border-top-success" />
                          </Col>
                        </Row>
                        <Row className="mt-16">
                          <Col sm={8}>
                            <FiledCard
                              title={t(
                                "translation:procurement.fields.originType.label"
                              )}
                            >
                              <SecondaryLabel className="font-weight-700">
                                {item.originType}
                              </SecondaryLabel>
                            </FiledCard>
                          </Col>
                          <Col sm={8}>
                            <FiledCard
                              title={t(
                                "translation:procurement.fields.originName.label"
                              )}
                            >
                              <SecondaryLabel className="font-weight-700">
                                {item.originName}
                              </SecondaryLabel>
                            </FiledCard>
                          </Col>
                        </Row>
                      </div>
                      <div className="w-1 border-left-muted"></div>
                      <div className="w-50">
                        <Row>
                          <Col
                            sm={24}
                            className="d-flex w-100 gap-1 align-items-center"
                          >
                            <SvgConverter
                              Icon={LocationIcon}
                              color={"danger-3"}
                            />
                            <SecondaryLabel className={"f-14 text-danger-3"}>
                              Destination
                            </SecondaryLabel>
                            <hr className="w-70 border-top-danger-3" />
                          </Col>
                        </Row>
                        <Row className="mt-16">
                          <Col sm={8}>
                            <FiledCard
                              title={t(
                                "translation:procurement.fields.destinationType.label"
                              )}
                            >
                              <SecondaryLabel className="font-weight-700">
                                {item.destinationType}
                              </SecondaryLabel>
                            </FiledCard>
                          </Col>
                          <Col sm={8}>
                            <FiledCard
                              title={t(
                                "translation:procurement.fields.destinationName.label"
                              )}
                            >
                              <SecondaryLabel className="font-weight-700">
                                {item.destinationName}
                              </SecondaryLabel>
                            </FiledCard>
                          </Col>
                        </Row>
                      </div>
                    </div>
                  </Col>
                </Fragment>
              </Row>
            </Card>
          ))}
        </div>
      </div>
    </div>
  );
}

export default TenderLaneInformationInfo;
