import { Col, Form, Row } from "antd";
import React, { Fragment, useEffect, useMemo, useState } from "react";
import SvgConverter from "../../../../../components/Common/SvgConverter";
import CollapsiblePanel from "../../../../../components/Tabs/CollapsiblePanel";
import TenderInformationTab from "../DetailsCard/Tabs/TenderInformationTab";
import { ReactComponent as ShipmentIcon } from "../../../../../../assets/icons/shipment-file.svg";
import { ReactComponent as DocsListIcon } from "../../../../../../assets/icons/docs-list.svg";
import FileDetails from "../../../Project/Component/AddProject/Component/FileDetails";
import { ReactComponent as NotesIcon } from "../../../../../../assets/icons/notes.svg";
import BidParametersCard from "../DetailsCard/Tabs/BidParameters";
import { useSelector, useDispatch } from "react-redux";
import TransportationProviderContent from "../../../TransportationProvider/TransportationProviderContent";
import Button from "../../../../../components/Buttons/Button";
import CommonModal from "../../../../../components/Modals/CommonModal";
import AddTransportationProvider from "../../../TransportationProvider/Component/AddTransportationProvider/AddTransportationProvider";
import TenderLaneInformationInfo from "./TenderLaneInformationInfo";
import TenderLaneInformationInfoModal from "./TenderLaneInformationInfoModal";
import { selectedLaneList } from "../../../../../../redux/Procurement/RouteConfig/Lane/ProcurementLaneActions";
import { getCarrierSearchReset } from "../../../../../../redux/common/CommonActions";
import BidParametersTab from "../DetailsCard/Tabs/BidParametersTab";
import { addNewBidParameterBtn } from "../../../../../../redux/Procurement/Tender/ProcurementTenderAction";

const AddTenderTab = ({
  tenderInformationForm,
  documentForm,
  bitParameterForm,
  activeKey,
  setActiveKey
}) => {
 
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [tenderLaneInfoModal, setTenderLaneInfoModal] = useState(false);
  const [transProvider, setTransProvider] = useState([]);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(addNewBidParameterBtn(true));
  }, [])
  

  const openModelForTransporationProviderHandler = (e) => {
    e.stopPropagation();
    setIsOpenModal(true);
    dispatch(getCarrierSearchReset());
  };

  const openLaneInfoModal = (e) => {
    e.stopPropagation();

    dispatch(selectedLaneList([]));
    setTenderLaneInfoModal(true);
  };

  const GenerateTitle = ({ title, Icon, size = 25 }) => {
    return (
      <Row gutter={10} align={"middle"}>
        <Col className="lh-0">
          <SvgConverter width={size} height={size} Icon={Icon} />
        </Col>
        <Col className="lh-0">
          <label className="lh-0 text-dark cursor-pointer">{title}</label>
        </Col>
      </Row>
    );
  };

  return (
    <Fragment>
      {isOpenModal && (
        <CommonModal
          className="w-65 h-90"
          open={isOpenModal}
          title={"Items Details"}
          onClose={() => setIsOpenModal(false)}
          customFooter={<></>}
        >
          <AddTransportationProvider
            selectedTransProvider={(data) => setTransProvider(data)}
          />
        </CommonModal>
      )}

      {tenderLaneInfoModal && (
        <CommonModal
          className="w-65 h-90"
          open={tenderLaneInfoModal}
          title={"Tender Lane Information"}
          onClose={() => setTenderLaneInfoModal(false)}
          customFooter={<></>}
        >
          <TenderLaneInformationInfoModal />
        </CommonModal>
      )}

      <CollapsiblePanel
        forceRender={true}
        handleTabClick={(keys) => setActiveKey(keys)}
        activeKey={activeKey}
        tabs={[
          {
            tabKey: "1",
            content: (
              <TenderInformationTab
                form={tenderInformationForm}
                isEdit={true}
              />
            ),
            header: (
              <GenerateTitle
                title={"Tender Information"}
                Icon={ShipmentIcon}
                size={25}
              />
            ),
          },
          {
            tabKey: "2",

            content: <FileDetails form={documentForm} />,
            header: (
              <GenerateTitle
                title={"Tender Documentation"}
                Icon={DocsListIcon}
                size={25}
              />
            ),
          },
          {
            tabKey: "3",
            content: <TenderLaneInformationInfo />,
            header: (
              <Row>
                <Col sm={20}>
                  <GenerateTitle
                    title={"Tender Lane Information"}
                    Icon={DocsListIcon}
                    size={25}
                  />
                </Col>
                <Col sm={4} className="d-flex justify-content-end">
                  <Button type="primary" onClick={openLaneInfoModal}>
                    Add
                  </Button>
                </Col>
              </Row>
            ),
          },
          {
            tabKey: "4",
            content: (
              // <BidParametersCard newData={true} form={bitParameterForm} />
              <BidParametersTab tenderId ={false} isCreateTender = {true}/>
            ),
            header: (
              <Row>
              <Col sm={20}>
              <GenerateTitle
                title={"Tender Bid Parameters"}
                Icon={DocsListIcon}
                size={25}
              />
              </Col>
              <Col sm={4} className="d-flex justify-content-end">
                <Button
                  type="primary"
                  onClick={()=>{
                    dispatch(addNewBidParameterBtn(true))
                  }}
                >
                  Add
                </Button>
              </Col>
            </Row>
             
            ),
          },
          {
            tabKey: "5",
            content: (
              <TransportationProviderContent
                data={transProvider}
                removeItem={(index) => {
                  let temp = [...transProvider];
                  temp.splice(index, 1);
                  setTransProvider(temp);
                }}
              />
            ),
            header: (
              <Row>
                <Col sm={20}>
                  <GenerateTitle
                    title={"Transportation Provider Search"}
                    Icon={NotesIcon}
                    size={25}
                  />
                </Col>
                <Col sm={4} className="d-flex justify-content-end">
                  <Button
                    type="primary"
                    onClick={openModelForTransporationProviderHandler}
                  >
                    Add
                  </Button>
                </Col>
              </Row>
            ),
          },
        ]}
      />
    </Fragment>
  );
};

export default AddTenderTab;
